<template>
	<div>
		<scroll-header></scroll-header>
		<gallary  :imgs="goodInfo['good_img']" :class="[showGallery?'fullscreen gallery':'scroll_banner']" @click.native="showGalleryFun" :type="1"></gallary>
		<span class="close" v-if="showGallery" @click="showGalleryFun">关闭</span>
<!-- 			<gallary  :imgs="goodInfo['good_img']" v-show="showGallery" class="fullscreen gallery" @close="showGalleryFun" :type="2"></gallary> -->
			<div class="box_wrap">
			<strong class="pro_name ellipsis2">{{goodInfo.good_name}}</strong>
				<div class="pro_info">
					<ul>
						<li>商品类型：<span>{{goodInfo.type_str}}</span></li>
						<li>收藏次数：<span>{{goodInfo.collects}}</span>次</li>
						<li>卖家名称：<span>{{goodInfo['seller_name']}}</span></li>
					</ul>
					<ul>
						<li>质保天数：<span>{{goodInfo.warranty_days}}</span>天</li>
						<li>浏览次数：<span>{{goodInfo.hisAmount}}</span>次</li>
						<li>商品价格：<span class="price">{{goodInfo.price}}</span>元</li>
					</ul>
				</div>
				<div class="pro_des">
					<h5 class="section_title">商品描述</h5>
					<div v-html="goodInfo['good_brief']"></div>
				</div>
			</div>
		<footer id="footer">
			<ul id="foodList">
				<router-link to="/Goods" tag="li">目录</router-link>
				<li><a @click="tips('该功能未开通')">客服</a></li>
				<li v-text="collects?'取消收藏':'收藏'" :class="{colled:collects}" @click="collectFun"></li>
				<li class="buy" @click="tips('无法完成真实购买哦')">立即购买</li>
			</ul>
		</footer>
	</div>
</template>
<script>
import scrollHeader from "common/scrollHeader.vue"
import tipShow from "@/assets/js/tips.js"
import gallary from "common/gallary.vue"
export default{
	data (){
		return {
			showGallery:false,
			collects:false,
			goodInfo:{
				'id':"223232",
				good_img:[
						"static/img/pic-ebee2f666.jpg",
						"static/img/pic-882a53fb2.jpg",
						"static/img/pic-ff807159de.png"],
				good_name:"影视会员，黄金爱奇艺包月仅12元，手机号密码登录，电影综艺一应俱全，价格低",
				type_str:"账号密码登录",
				collects:10,
				seller_name:"***积极",
				hisAmount:203,
				warranty_days:26,
				price:12,
				good_brief:"<p>注：商品详情页都直指向同一页，就不重复耗时地操作去填写每一条数据啦~</p><p>电脑端不用验证 直接手机号+密码登录 手机端需要短信验证 有任何问题请联系我手机（如登录设备过多、密码错误、需要验证码等） 联系我帮您处理 谢谢老板们的大力支持 注：日租，十小时租更优惠</p>"
			}
		}
	},
	methods:{
		showGalleryFun (){
			this.showGallery=!this.showGallery;
			var cur=this.$route.fullPath
		},
		collectFun (){
			this.collects=!this.collects;
			this.collects?tipShow('收藏成功'):tipShow('取消收藏成功')
		},
		tips (msg){
			tipShow(msg)
		}
	},
	components:{
		scrollHeader,
		gallary
	}
}
	
</script>
<style scoped>
	/*@import url(../../assets/css/order_common.css);*/
	 .pro_pic{
	     position: absolute;
	     top: 0rem;
	     width: 100%;
	     height: 100%;
	}
	 .pro_picwrap{
	     padding-bottom: 60%;
	     width: 100%;
	     position: relative;
	}
	 .pro_info{
	     display: flex;
	}
	 .pro_info ul{
	     padding-left: 0.08rem;
	}
	 .pro_info ul:nth-child(1){
	     width:55%;
	     padding-right: 4%;
	     box-sizing: border-box;
	}
	 .pro_info li{
	     line-height: 0.21rem;
	     overflow: hidden;
	     text-overflow: ellipsis;
	     white-space: nowrap;
	}
	 .pro_info span{
	     padding: 0rem 0.03rem;
	}
	 .price{
	     color:crimson;
	}
	 .pro_name{
	     font-weight: bold;
	     font-size: 0.16rem;
	     color: #333;
	     line-height:0.24rem;
	     margin: 0.1rem 0rem;
	}
	 .pro_des >>> p{
	     text-indent: 2em;
	     line-height: 0.21rem;
	     margin: 12px 0px;
	}
	.pro_des>div:empty:before{
		content: "卖家没有添加商品描述";
		color: #bbb;
	}
	.section_title{
	     font-weight: bold;
	     color: #333;
	     border-bottom:0.01rem solid #bbbbbb;
	     line-height:0.31rem;
	     margin: 0.15rem 0rem 0rem;
	     font-size: 0.14rem;
	}
	 footer{
	     width: 100%;
	     padding: 0;
	     position: fixed;
	     bottom: 0px;
	     left: 0px;
	     background: #fff;
	     box-shadow: 0px -1px 3px rgba(88,88,88,0.2);
	}
	 footer > ul{
	     width: 100%;
	     display: flex;
	}
	 footer li{
	     height: 0.48rem;
	     width: 20%;
	     line-height: 0.74rem;
	     text-align: center;
	     background-size: 0.22rem;
	     background-position: center 0.05rem;
	     background-repeat: no-repeat;
	}
	 footer li:nth-of-type(1) {
	     background-image: url(../../assets/img/icon_category.png);
	}
	 footer li:nth-of-type(2) {
	     background-image: url(../../assets/img/icon_qq.png);
	}
	 footer li:nth-of-type(3) {
	     background-image: url(../../assets/img/icon_star.png) 
	}
	 footer li.colled {
	     background-image: url(../../assets/img/icon_stared.png) 
	}
	 footer li:nth-of-type(4) {
	     width: 40%;
	     color: #fff;
	     font-size: 0.16rem;
	     line-height: 0.48rem;
	     background-color: #e6292c;
	}

	 .box_wrap{
	     box-sizing: border-box;
	     width: 100%;
	     padding: 0.1rem;
	}

	/*gallery*/
	.scroll_banner{
		width: 100%;
		height: 0px;
		padding-bottom: 63%;
		overflow: hidden;
		position: relative;
	}
	.scroll_banner >>> img{
		width: 100%;
	}
	.gallery{
		z-index: 99;
		background: #000;
	}
	.gallery >>> img{
		object-fit: scale-down; 
		width: 100%;
		height: 100vh;
	}	 
	.scroll_banner >>>.swiper-pagination1{
		color: #eee;
		font-size: 12px;
		width: 40px;
		text-align: center;
		left: auto;
		background: rgba(0,0,0,0.3);
		border-radius: 12px;
		right: 20px;
		height: 18px;
		line-height: 18px;
		bottom: 30px;
		transition:none;
	}
	.gallery >>>.swiper-pagination1{
		color: #fff;
		font-size: 12px;
		bottom: 60px;
		transition:none;
	}
	.close{
		z-index: 100;
		position: fixed;
		left: 20px;
		top: 30px;
		color: #bbb;
		background: rgba(0,0,0,0.3);
		border-radius: 12px;
		right: 20px;
		height: 18px;
		line-height: 18px;
	}

</style>


